// Light theme
// -----------------------------------------
html.lw-light-theme {
    $primaryColor: #d2009c;
    $lightGray: #f5f5f5;

    body {
        color: #3c3c3c;
        /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#949494+0,eeeeee+64 */
        background-color: rgb(148, 148, 148);
        background: rgb(148, 148, 148);

        /* Old browsers */
        background: -moz-linear-gradient(top, rgba(148, 148, 148, 1) 0%, rgba(238, 238, 238, 1) 64%);
        /* FF3.6-15 */
        background: -webkit-linear-gradient(top, rgba(148, 148, 148, 1) 0%, rgba(238, 238, 238, 1) 64%);
        /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom, rgba(148, 148, 148, 1) 0%, rgba(238, 238, 238, 1) 64%);
        /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#949494', endColorstr='#eeeeee', GradientType=0);
        /* IE6-9 */
    }

    #wrapper #content-wrapper {
        box-shadow: 0 0.15rem 1.75rem 0 rgba(58, 59, 69, 0.15) !important;
        background-color: white !important;
        padding-right: 22px;
    }

    .sidebar-dark .sidebar-brand {
        border-bottom-left-radius: 0;
    }

    .card,
    .form-control {
        background-color: $lightGray;
        color: #555555;

        .card-header {
            background-color: #d8d8d8;
            border-bottom: 1px solid #a9a9a9;
            color: #444444;
        }

        &:focus {
            background-color: #ffffff;
        }
    }

    .card table,
    .card table thead th {
        background: #fafafa;
    }

    .sidebar .card {
        .card-header {
            background-color: transparent;
            border-bottom: 1px solid #e3e3e3;
        }
        background-color: transparent;
        border-bottom: 1px solid #e3e3e3;
        border-radius: 0;
    }

    .bg-dark {
        background-color: $lightGray !important;
        color: black;
    }

    .topbar.navbar-light .navbar-nav .nav-item .nav-link {
        color: #ffffff;
    }

    .lw-page-content,
    .lw-page-bg {
        background-color: #ffffff !important;
    }

    a.dropdown-item.text-primary {
        &:active {
            color: #ffffff !important;
        }
    }

    .sidebar-dark .nav-item .nav-link {
        color: #3c3c3c;

        i {
            color: #3c3c3c;
        }

        &:active,
        &focus,
        &:hover {
            color: #747474;

            i {
                color: #747474;
            }
        }
    }

    .sidebar {
        background-color: #ffffff;
    }

    .sidebar-dark hr.sidebar-divider {
        border-top: 1px solid rgba(101, 101, 101, 0.15);
    }

    .text-gray-200 {
        color: #3c3c3c !important;
    }

    .text-gray-300 {
        color: #b9b9b9 !important;
    }

    .lw-ad-block-h90,
    .lw-sidebar-ads-container {
        background-color: white;
        color: #3e3e3e;
    }

    .lw-horizontal-container .lw-user-photo {
        border: 1px solid #ababab;
    }

    nav.navbar.navbar-expand.navbar-light.bg-dark.topbar.mb-4.static-top.shadow,
    .sidebar-dark .sidebar-brand {
        background-color: #424242 !important;
        color: white;
    }

    .lw-user-thumbnail-block {
        &:hover {
            background-color: #b1b3c6;
        }

        .card-title .text-secondary {
            color: #3c3c3c !important;

            &:hover {
                color: #3c3c3c !important;
                background-color: #b1b3c6;
            }
        }
    }

    .list-group-item.list-group-item-action {
        background-color: $lightGray;
        color: #555555;

        &.active {
            background-color: $primaryColor;
            color: white;
        }
    }


    .list-group-item {
        border-color: #d8d8d8;
    }

    .chart-area {
        background-color: #d8d8d8;
    }

    .form-group .form-control {
        border-bottom: 1px solid #bebebe;
    }

    .form-control {
        border-color: #c6c6c6;
    }

    .lw-fieldset {
        border-color: #bebebe;
    }

    .table {
        color: #696969;
        border-color: #dfdfdf;

        .table-hover tbody tr:hover {
            background-color: rgba(167,167,167, 0.8);
        }

        th,
        td {
            border-color: #dfdfdf;
        }

        thead th {
            border-color: #dfdfdf;
            background-color: #dfdfdf;
        }
    }

    .page-link {
        border-color: #dfdfdf;
        background-color: #a3a3a3;
        color: #fdfdfd;
    }

    // a:not(.dropdown-item):not(.btn):not(.list-group-item),
    .sidebar-dark .nav-item.active .nav-link i,
    .sidebar-dark .nav-item.active .nav-link {
        color: $primaryColor;
    }

    .text-primary {
        color: $primaryColor !important;
    }

    .btn-primary {
        background-color: $primaryColor;
        border-color: $primaryColor;
        color: #fff;
    }

    .border-left-primary {
        border-color: $primaryColor !important;
    }

    .btn.btn-dark {
        color: #000;
        background-color: #fff;
        border-color: #dbdbda;

        &:hover {
            color: #fff;
            background-color: #a1a1a1;
            border-color: darkgrey;
        }
    }

    .selectize-input.full {
        background-color: $lightGray;
    }

    .selectize-control {
        border-bottom: 1px solid #bebebe;
    }

    .input-group-text {
        color: #ffffff;
    }

    .form-control:disabled,
    .form-control[readonly] {
        background-color: #cecece !important;
        color: #1f1f1f !important;
    }

    .nav-tabs .nav-link.active,
    .nav-tabs .nav-link:hover,
    .nav-tabs .nav-link:active,
    .nav-tabs .nav-link:focus {
        border-color: #3a3b44 #3a3b44 $lightGray;
        background-color: $lightGray;
        color: #535353;
    }

    .lw-login-register-page {
        .card,
        .lw-update-wizard {
            background-color: rgba(200, 200, 200, 0.7);
            color: #fff;
        }

        .selectize-input.full,
        .form-group .selectize-control.form-control {
            color: #2f2f2f;
            background-color: #f5f5f5;
        }
    }


    .masthead {
        background: linear-gradient(to bottom, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.2) 75%, #ffffff 100%), url("../../imgs/home/bg-masthead.jpg");
        background-position: center;
        background-repeat: no-repeat;
        background-attachment: scroll;
        background-size: cover;

        .text-white-50 {
            color: rgba(0, 0, 0, 0.5) !important;
        }
    }

    .bg-dark {
        background-color: $lightGray !important;
        color: black;
    }

    footer.bg-dark {
        background-color: #fff !important;
    }

    footer.text-white-50 {
        color: rgba(50, 50, 50, 0.5) !important;
    }

    footer.sticky-footer {
        background-color: #e5e5e5 !important;
    }

    .features-section,
    .premium-section {
        .text-white-50 {
            color: rgba(50, 50, 50, 0.5) !important;
        }

        .text-white {
            color: #000000 !important;
        }
    }

    .lw-like-dislike-box:last-child {
        border-left: 1px solid #d8d8d8;
    }

    @media (min-width: 992px) {
        #mainNav.navbar-shrink {
            background-color: rgba(220, 220, 220, 0.29);
            color: black;
        }
    }

    .form-group,
    .input-group {
        .form-control:focus {
            background-color: #ffffff;
        }
    }

    .lw-messenger {
        background-color: #ffffff;
        color: #2d2d2d;

        .lw-messenger-footer {
            background-color: #ffffff;
            border-top: 1px solid #dcdcdc;
        }

        .lw-messenger-header {
            color: #191919;
            // background-color: #f5f5f5;
            background-color: #ffffff;
            border-bottom: 1px solid #dcdcdc;
        }

        .lw-profile-name {
            color: #2a2a2a;
        }

        .lw-messenger-sidebar {
            background-color: #f5f5f5;

            .lw-messenger-contact-list .list-group-item.list-group-item-action:not(.active):hover {
                background-color: #cacaca;
            }
        }

    }

    .lw-group-radio-option-img {
        background-color: #e6ebfd;
        border: 1px solid #cfd3e3;

        img {
            border: 1px solid #cfd3e3;
        }

        .lw-credit-package-name {
            background-color: #ffffff;
            color: #858796;
        }
    }

    #lwSendGiftForm .lw-group-radio-option-img {
        color: #3a3b45;
    }

    .lw-cookie-policy-container {
        background: rgba(0, 0, 0, 0.53);
        color: white;
    }

    .page-item.disabled .page-link {
        border-color: #dadada;
        background-color: #bcbcbc;
        color: rgba(255,255,255, 0.6);
    }

    .sidebar-dark #sidebarToggle {
        background-color: rgba(33,33,33,0.2);
    }

    .lw-user-gift-container {
        border: 1px solid rgba(169,169,169, 0.44);
        background-color: #ffffff;
    }
    .lw-credit-balance {
        background-color: #f3f3f3;
    }

    .lw-premium-feature-item {
        background-color: #ffffff;
        border: 1px solid #d6d6d6;
    }

    .lw-premium-plan-radio-option {
        &:hover {
            color: #ffffff;
            background-color: #19a975;
        }

        &.active {
            color: #ffffff;
            border: 1px solid #22a06e;
            background-color: #19a975;
        }
    }

    .lw-premium-plan-right-border {
        border-right: 1px solid #dfdfdf !important;
    }
}